import React, { useEffect, useRef, useState } from "react";
import { Html5Qrcode } from "html5-qrcode";
import { useNavigate } from "react-router-dom";
import "./CheckinPage.css";

export default function CheckinPage() {
  const html5QrCodeRef = useRef(null);
  const [checkedIn, setCheckedIn] = useState(false);
  const [todaysEvent, setTodaysEvent] = useState(null);
  const [errorMsg, setErrorMsg] = useState("");
  const navigate = useNavigate();
  const isRunning = useRef(false);

  useEffect(() => {
    fetch("http://localhost:5000/api/todays-event")
      .then(res => res.json())
      .then(events => {
        if (Array.isArray(events) && events.length > 0) {
          setTodaysEvent(events[0]);
        }
      });
  }, []);

  useEffect(() => {
    // Remove any existing #reader element
    const oldReader = document.getElementById("reader");
    if (oldReader && oldReader.parentNode) {
      oldReader.parentNode.removeChild(oldReader);
    }

    // Create a new #reader element
    const readerDiv = document.createElement("div");
    readerDiv.id = "reader";
    readerDiv.style.width = "800px";
    readerDiv.style.height = "800px";
    const container = document.getElementById("reader-container");
    if (container) {
      container.appendChild(readerDiv);
    }

    html5QrCodeRef.current = new Html5Qrcode("reader");
    html5QrCodeRef.current
      .start(
        { facingMode: "environment" },
        { fps: 10, qrbox: { width: 800, height: 800 } },
        qrCodeMessage => {
          try {
            const data = JSON.parse(qrCodeMessage);
            const registrationId = data.registrationId;
            if (!registrationId) throw new Error("No registrationId in QR code");

            // Call the check-in endpoint
            fetch(`http://localhost:5000/api/registrations/${registrationId}/checkin`, {
              method: 'POST'
            })
              .then(res => res.json())
              .then(data => {
                if (data.success) {
                  setCheckedIn(true);
                  setErrorMsg("");
                  setTimeout(() => {
                    navigate("/");
                  }, 2000); // 2 seconds
                } else {
                  if (data.error === "Already scanned") {
                    setErrorMsg("This QR code has already been scanned.");
                    setTimeout(() => {
                      navigate("/");
                    }, 2000); // 2 seconds
                  } else {
                    setErrorMsg("Check-in failed!");
                  }
                }
              });
          } catch (e) {
            setErrorMsg('Invalid QR code');
          }
        },
        errorMessage => {
          // Optionally log errors
        }
      )
      .then(() => {
        isRunning.current = true;
      })
      .catch(() => {
        isRunning.current = false;
      });

    return () => {
      if (html5QrCodeRef.current) {
        // Only call stop if running, and check for Promise before chaining
        let stopPromise;
        if (isRunning.current && typeof html5QrCodeRef.current.stop === "function") {
          stopPromise = html5QrCodeRef.current.stop();
        }
        if (stopPromise && typeof stopPromise.then === "function") {
          stopPromise
            .then(() => {
              if (typeof html5QrCodeRef.current.clear === "function") {
                const clearPromise = html5QrCodeRef.current.clear();
                if (clearPromise && typeof clearPromise.catch === "function") {
                  clearPromise.catch(() => {});
                }
              }
              isRunning.current = false;
            })
            .catch(() => {});
        } else if (typeof html5QrCodeRef.current.clear === "function") {
          const clearPromise = html5QrCodeRef.current.clear();
          if (clearPromise && typeof clearPromise.catch === "function") {
            clearPromise.catch(() => {});
          }
        }
        html5QrCodeRef.current = null;
      }
      // Remove the #reader element on unmount
      const reader = document.getElementById("reader");
      if (reader && reader.parentNode) {
        reader.parentNode.removeChild(reader);
      }
    };
    // eslint-disable-next-line
  }, [navigate]);

  return (
    <div className="checkin-bg">
      <div className="checkin-aspect">
        <div className="checkin-header-centered">
          <img src="/NGO.png" alt="Logo" className="checkin-logo" />
          <span className="event-org-info">
            Organization Name | Tagline | Info
          </span>
          <button className="checkin-home-btn" onClick={() => window.location.href = "/"}>
            <span role="img" aria-label="home">🏠</span>
          </button>
        </div>

        <div className="checkin-main">
          {todaysEvent && (
            <>
              <div className="checkin-welcome-msg">Welcome to {todaysEvent.name}</div>
              <div className="checkin-event-banner">
                <img src={`http://localhost:5000${todaysEvent.banner}`} alt="Event Banner" className="checkin-banner-img" />
              </div>
            </>
          )}
          <div className="checkin-title">Scan your QR code here</div>
          <div className="checkin-qr-scanner">
            <div id="reader-container"></div>
          </div>
          {checkedIn && (
            <div style={{ color: "green", fontWeight: "bold", fontSize: "4rem", marginTop: 16 }}>
              You have successfully checked in!
            </div>
          )}
          {errorMsg && (
            <div style={{ color: "red", fontWeight: "bold", fontSize: "4rem", marginTop: 16 }}>
              {errorMsg}
            </div>
          )}
        </div>

        <div className="checkin-footer">
          <span className="footer-icon">📍</span>
          <a href="https://maps.google.com/?q=123+Street,+City+ST+12345" target="_blank" rel="noopener noreferrer" className="footer-link">123 Street, City ST 12345</a>
          <span className="footer-icon">📞</span>
          <a href="tel:6099372800" className="footer-link">609 937 2800 / 609 937 2806</a>
          <span className="footer-icon">✉️</span>
          <a href="mailto:NGO@gmail.com" className="footer-link">NGO@gmail.com</a>
        </div>
      </div>
    </div>
  );
}