import React, { useState } from "react";
import Lottie from "lottie-react";
import loginLottie from "./login-lottie.json";
import { useNavigate } from "react-router-dom";
import "./App.css";
import "./AdminPage.css";

function AdminPage() {
  const navigate = useNavigate();
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  // Demo credentials (replace with your real logic)
  const VALID_USER = "admin";
  const VALID_PASS = "password123";

  const handleSubmit = (e) => {
    e.preventDefault();
    if (username === VALID_USER && password === VALID_PASS) {
      setError("");
      // Redirect or do something on success
      navigate("/event-details");
    } else {
      setError("Invalid username or password.");
    }
  };

  return (
    <div className="aspect-wrapper">
      <header className="header">
        <img src="/NGO.png" alt="Sai Logo" className="logo" style={{ width: '390px', height: '390px' }} />
        <span className="event-org-info">
          Organization Name | Tagline | Info
        </span>
        <button className="admin-btn" onClick={() => navigate("/")}>
          <span className="admin-icon" role="img" aria-label="home">🏠</span>
        </button>
      </header>
      <main className="main-content">
        <h1 className="welcome-title">Admin Login</h1>
        <div className="admin-lottie">
          <Lottie animationData={loginLottie} loop={true} />
        </div>
        <form className="admin-login-form" onSubmit={handleSubmit}>
          <div className="admin-form-row">
            <label htmlFor="username">Email / Username:</label>
            <input
              id="username"
              type="text"
              className="admin-input"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              autoComplete="username"
              required
            />
          </div>
          <div className="admin-form-row">
            <label htmlFor="password">Password:</label>
            <input
              id="password"
              type="password"
              className="admin-input"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              autoComplete="current-password"
              required
            />
          </div>
          {error && <div className="admin-error">{error}</div>}
          <button type="submit" className="admin-login-btn">Login</button>
        </form>
      </main>
      <footer className="event-footer event-footer-dark">
        <span className="footer-icon">📍</span>
        <a href="https://maps.google.com/?q=123+Street,+City+ST+12345" target="_blank" rel="noopener noreferrer" className="footer-link">123 Street, City ST 12345</a>
        <span className="footer-icon">📞</span>
        <a href="tel:6099372800" className="footer-link">609 937 2800 / 609 937 2806</a>
        <span className="footer-icon">✉️</span>
        <a href="mailto:NGO@gmail.com" className="footer-link">NGO@gmail.com</a>
      </footer>
    </div>
  );
}

export default AdminPage;
