import React, { useState, useEffect } from "react";
import Lottie from "lottie-react";
import registerLottie from "./register-lottie.json";
import { useNavigate } from "react-router-dom";
import "./RegisterPage.css";

function RegisterPage() {
  const [form, setForm] = useState({ name: "", phone: "", email: "" });
  const [success, setSuccess] = useState(false);
  const [todaysEvent, setTodaysEvent] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    fetch("http://localhost:5000/api/todays-event")
      .then(res => res.json())
      .then(events => {
        if (Array.isArray(events) && events.length > 0) {
          setTodaysEvent(events[0]);
        }
      });
  }, []);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const payload = { ...form, event_id: todaysEvent?.id };
      const response = await fetch('http://localhost:5000/api/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });
      const data = await response.json();
      if (data.success) {
        setSuccess(true);
        setTimeout(() => {
          navigate("/");
        }, 3000); // 3 seconds before redirect
      } else {
        alert('Registration failed: ' + (data.error || 'Unknown error'));
      }
    } catch (err) {
      alert('Error connecting to server.');
    }
  };

  return (
    <div className="register-bg">
      <div className="register-aspect">
        <header className="register-header-centered">
          <img src="/NGO.png" alt="Sai Logo" className="register-logo" />
          <span className="event-org-info">
            Organization Name | Tagline | Info
          </span>
          <button className="register-home-btn" onClick={() => navigate("/")}>
            <span role="img" aria-label="home" className="register-home-icon">🏠</span>
          </button>
        </header>
        <main className="register-main">
          {todaysEvent && (
            <>
              <div className="register-welcome-msg">Welcome to {todaysEvent.name}</div>
              <div className="register-event-banner">
                <img src={`http://localhost:5000${todaysEvent.banner}`} alt="Event Banner" className="register-banner-img" />
              </div>
            </>
          )}
          <div className="register-title">Enter Your Details To Register For The Event</div>
          <div className="register-row">
            <div className="register-lottie">
              <Lottie animationData={registerLottie} loop={true} />
            </div>
            {success ? (
              <div style={{
                color: 'green',
                fontWeight: 'bold',
                fontSize: '4rem',
                textAlign: 'center',
                margin: '32px 0',
                background: '#fff',
                borderRadius: '12px',
                padding: '24px',
                boxShadow: '0 2px 8px rgba(0,0,0,0.08)'
              }}>
                <div style={{fontSize: '4rem', marginBottom: 12}}>✅</div>
                Registration successful!<br/>
                You will receive a confirmation email with your QR code.<br/>
                Please scan the QR at the kiosk to complete check-in.
              </div>
            ) : (
              <form onSubmit={handleSubmit} className="register-form">
                <div className="register-form-row">
                  <label className="register-label" htmlFor="name">Full Name:</label>
                  <input
                    className="register-input"
                    type="text"
                    id="name"
                    name="name"
                    value={form.name}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="register-form-row">
                  <label className="register-label" htmlFor="phone">Phone Number:</label>
                  <input
                    className="register-input"
                    type="tel"
                    id="phone"
                    name="phone"
                    value={form.phone}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="register-form-row">
                  <label className="register-label" htmlFor="email">Email Address:</label>
                  <input
                    className="register-input"
                    type="email"
                    id="email"
                    name="email"
                    value={form.email}
                    onChange={handleChange}
                    required
                  />
                </div>
                <button className="register-btn" type="submit">Register</button>
              </form>
            )}
          </div>
        </main>
        <footer className="event-footer event-footer-dark">
          <span className="footer-icon">📍</span>
          <a href="https://maps.google.com/?q=123+Sample+Street,+Sample+City+ST+12345" target="_blank" rel="noopener noreferrer" className="footer-link">123 Street, City ST 12345</a>
          <span className="footer-icon">📞</span>
          <a href="tel:6099372800" className="footer-link">609 937 2800 / 609 937 2806</a>
          <span className="footer-icon">✉️</span>
          <a href="mailto:NGO@gmail.com" className="footer-link">NGO@gmail.com</a>
        </footer>
      </div>
    </div>
  );
}

export default RegisterPage; 