import React, { useEffect, useState } from "react";
import "./EventDetailsPage.css";
import { useNavigate } from "react-router-dom";

function EventDetailsPage() {
  const [events, setEvents] = useState([]);
  const [filters, setFilters] = useState({
    name: "",
    date: "",
    time: "",
    location: "",
  });
  const [editingEvent, setEditingEvent] = useState(null);
  const [newEvent, setNewEvent] = useState({
    name: "",
    date: "",
    time: "",
    location: "",
    banner: null,
  });
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();

  // Fetch all events on mount
  useEffect(() => {
    fetch("http://localhost:5000/api/events")
      .then(res => res.json())
      .then(data => setEvents(data));
  }, []);

  // Filtered events
  const filteredEvents = events.filter(event =>
    event.name.toLowerCase().includes(filters.name.toLowerCase()) &&
    event.date.includes(filters.date) &&
    event.time.includes(filters.time) &&
    event.location.toLowerCase().includes(filters.location.toLowerCase())
  );

  // Handle add/edit form changes
  const handleInputChange = (e, isEdit = false) => {
    const { name, value, files } = e.target;
    if (isEdit) {
      setEditingEvent({ ...editingEvent, [name]: files ? files[0] : value });
    } else {
      setNewEvent({ ...newEvent, [name]: files ? files[0] : value });
    }
  };

  // Add new event
  const handleAddEvent = async (e) => {
    e.preventDefault();
    setMessage(""); setError("");
    const formData = new FormData();
    formData.append("name", newEvent.name);
    formData.append("date", newEvent.date);
    formData.append("time", newEvent.time);
    formData.append("location", newEvent.location);
    if (newEvent.banner) formData.append("banner", newEvent.banner);

    try {
      const res = await fetch("http://localhost:5000/api/events", {
        method: "POST",
        body: formData,
      });
      const data = await res.json();
      if (data.success) {
        setEvents([...events, data.event]);
        setNewEvent({ name: "", date: "", time: "", location: "", banner: null });
        setMessage("Event added successfully!");
      } else {
        setError(data.error || "Failed to add event.");
      }
    } catch {
      setError("Failed to add event.");
    }
  };

  // Edit event
  const handleEditEvent = async (e) => {
    e.preventDefault();
    setMessage(""); setError("");
    const formData = new FormData();
    formData.append("name", editingEvent.name);
    formData.append("date", editingEvent.date);
    formData.append("time", editingEvent.time);
    formData.append("location", editingEvent.location);
    if (editingEvent.banner) formData.append("banner", editingEvent.banner);

    try {
      const res = await fetch(`http://localhost:5000/api/events/${editingEvent.id}`, {
        method: "PUT",
        body: formData,
      });
      const data = await res.json();
      if (data.success) {
        setEvents(events.map(ev => ev.id === editingEvent.id ? data.event : ev));
        setEditingEvent(null);
        setMessage("Event updated successfully!");
      } else {
        setError(data.error || "Failed to update event.");
      }
    } catch {
      setError("Failed to update event.");
    }
  };

  // Delete event
  const handleDeleteEvent = async (id) => {
    const res = await fetch(`http://localhost:5000/api/events/${id}`, { method: "DELETE" });
    if (res.ok) setEvents(events.filter(ev => ev.id !== id));
  };

  // Filter change
  const handleFilterChange = (e) => {
    setFilters({ ...filters, [e.target.name]: e.target.value });
  };

  const handleLogout = () => {
    // Redirect to admin login page
    navigate("/admin");
  };

  const handleRegistration = () => {
    // Navigate to admin registrations page
    navigate('/admin/registrations');
  };

  const handleAddEventRow = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("name", newEvent.name);
    formData.append("date", newEvent.date);
    formData.append("time", newEvent.time);
    formData.append("location", newEvent.location);
    if (newEvent.banner) formData.append("banner", newEvent.banner);

    const res = await fetch("http://localhost:5000/api/events", {
      method: "POST",
      body: formData,
    });
    const data = await res.json();
    if (data.success) {
      setEvents([...events, data.event]);
      setNewEvent({ name: "", date: "", time: "", location: "", banner: null });
    }
  };

  useEffect(() => {
    if (message || error) {
      const timer = setTimeout(() => {
        setMessage("");
        setError("");
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [message, error]);

  return (
    <div className="event-bg">
      <div className="event-aspect">
        <header className="event-header event-header-centered">
        <img src="/NGO.png" alt="Sai Logo" className="event-logo" />
          <span className="event-org-info">
            Organization Name | Tagline | Info
          </span>
          <div className="event-tabs">
            <button
              className="event-tab-btn"
              onClick={handleRegistration}
              title="Registration Details"
            >
              <svg width="28" height="28" viewBox="0 0 48 48" fill="none" style={{ verticalAlign: 'middle' }}>
                <rect x="8" y="8" width="32" height="32" rx="4" stroke="#8B1C1C" strokeWidth="4" fill="none"/>
                <line x1="16" y1="20" x2="32" y2="20" stroke="#8B1C1C" strokeWidth="4" strokeLinecap="round"/>
                <line x1="16" y1="28" x2="24" y2="28" stroke="#8B1C1C" strokeWidth="4" strokeLinecap="round"/>
                <polyline points="28,36 34,42 44,32" fill="none" stroke="#8B1C1C" strokeWidth="4" strokeLinecap="round" strokeLinejoin="round"/>
              </svg>
            </button>
            <button
              className="event-tab-btn"
              onClick={() => window.location.href = "/"}
              title="Home"
            >
              <span role="img" aria-label="home" style={{ fontSize: '3rem', verticalAlign: 'middle' }}>🏠</span>
            </button>
          </div>
        </header>
        <main className="event-main">
          <h2 className="event-title">Event Details</h2>
          {message && <div style={{ color: "green", fontWeight: "bold", margin: "10px 0" }}>{message}</div>}
          {error && <div style={{ color: "red", fontWeight: "bold", margin: "10px 0" }}>{error}</div>}
          <div className="event-table-container">
            <table className="event-table">
              <thead>
                <tr>
                  <th>Event Name</th>
                  <th>Date</th>
                  <th>Time</th>
                  <th>Location</th>
                  <th>Banner</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <input
                      name="name"
                      placeholder="Event Name"
                      value={newEvent.name}
                      onChange={handleInputChange}
                      required
                    />
                  </td>
                  <td>
                    <input
                      name="date"
                      type="date"
                      value={newEvent.date}
                      onChange={handleInputChange}
                      required
                    />
                  </td>
                  <td>
                    <input
                      name="time"
                      type="time"
                      value={newEvent.time}
                      onChange={handleInputChange}
                      required
                    />
                  </td>
                  <td>
                    <input
                      name="location"
                      placeholder="Location"
                      value={newEvent.location}
                      onChange={handleInputChange}
                      required
                    />
                  </td>
                  <td>
                    <input
                      name="banner"
                      type="file"
                      accept="image/*"
                      onChange={handleInputChange}
                    />
                  </td>
                  <td>
                    <button className="event-add-btn" onClick={handleAddEventRow}>Add Event</button>
                  </td>
                </tr>
                {events.map(ev => (
                  editingEvent && editingEvent.id === ev.id ? (
                    <tr key={ev.id}>
                      <td>
                        <input
                          value={editingEvent.name}
                          onChange={e => setEditingEvent({ ...editingEvent, name: e.target.value })}
                        />
                      </td>
                      <td>
                        <input
                          type="date"
                          value={editingEvent.date}
                          onChange={e => setEditingEvent({ ...editingEvent, date: e.target.value })}
                        />
                      </td>
                      <td>
                        <input
                          type="time"
                          value={editingEvent.time}
                          onChange={e => setEditingEvent({ ...editingEvent, time: e.target.value })}
                        />
                      </td>
                      <td>
                        <input
                          value={editingEvent.location}
                          onChange={e => setEditingEvent({ ...editingEvent, location: e.target.value })}
                        />
                      </td>
                      <td>
                        <input
                          type="file"
                          name="banner"
                          onChange={e => setEditingEvent({ ...editingEvent, banner: e.target.files[0] })}
                        />
                      </td>
                      <td>
                        <button onClick={handleEditEvent}>Save</button>
                        <button onClick={() => setEditingEvent(null)}>Cancel</button>
                      </td>
                    </tr>
                  ) : (
                    <tr key={ev.id}>
                      <td>{ev.name}</td>
                      <td>{ev.date}</td>
                      <td>{ev.time}</td>
                      <td>{ev.location}</td>
                      <td>{ev.banner && <img src={`http://localhost:5000${ev.banner}`} alt="banner" width={60} />}</td>
                      <td>
                        <button onClick={() => setEditingEvent(ev)}>Edit</button>
                        <button onClick={() => handleDeleteEvent(ev.id)}>Delete</button>
                      </td>
                    </tr>
                  )
                ))}
              </tbody>
            </table>
          </div>
          <button className="event-logout-btn" onClick={handleLogout}>Logout</button>
        </main>
        <footer className="event-footer event-footer-dark">
          <span className="footer-icon">📍</span>
          <a href="https://maps.google.com/?q=123+Street,+City+ST+12345" target="_blank" rel="noopener noreferrer" className="footer-link">123 Street, City ST 12345</a>
          <span className="footer-icon">📞</span>
          <a href="tel:6099372800" className="footer-link">609 937 2800 / 609 937 2806</a>
          <span className="footer-icon">✉️</span>
          <a href="mailto:NGO@gmail.com" className="footer-link">NGO@gmail.com</a>
        </footer>
      </div>
    </div>
  );
}

export default EventDetailsPage;
